.App {
  display: flex;
  flex-direction: column;
  background-color: white;
  margin: 0 !important;
  padding: 0 !important;
}

/* Creation of grain-y effect in the background */
.background {
  background-image: url(data:image/png;base64,);
}

.title {
  height: 3.5vw;
  align-self: flex-end;
}

.scroll {
  height: 20vh;
  width: 20vh;
}
